body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.auth-container {
  margin-top: 50px;
  padding: 15px;
  width: 300px;
  border: 1px solid #ebe2eb;
  border-radius: 5px;
  box-shadow: 7px 7px 6px 2px rgba(224,224,224,0.7);
}
#home {
  display: flex;
  justify-content: center; }
  #home .buttons {
    display: flex;
    flex-direction: column; }

@keyframes lds-ripple {
  0% {
    top: 96px;
    left: 96px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 18px;
    left: 18px;
    width: 156px;
    height: 156px;
    opacity: 0;
  }
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 96px;
    left: 96px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 18px;
    left: 18px;
    width: 156px;
    height: 156px;
    opacity: 0;
  }
}
.lds-ripple {
  position: relative;
}
.lds-ripple div {
  box-sizing: content-box;
  position: absolute;
  border-width: 4px;
  border-style: solid;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(1) {
  border-color: #e90c59;
}
.lds-ripple div:nth-child(2) {
  border-color: #fe718d;
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
.lds-ripple {
  width: 24px !important;
  height: 24px !important;
  -webkit-transform: translate(-20px, -20px) scale(0.2) translate(20px, 20px);
  transform: translate(-20px, -20px) scale(0.2) translate(20px, 20px);
}
#login {
  display: flex;
  justify-content: center;
}
#register {
  display: flex;
  justify-content: center;
}
.input-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px; }

.options-container {
  display: flex;
  flex-direction: row; }
  @media (max-width: 414px) {
    .options-container {
      flex-direction: column; } }

.additional-options {
  flex-basis: 35%;
  margin-top: 10px;
  margin-right: 20px; }
  @media (max-width: 414px) {
    .additional-options {
      flex-basis: 100%; } }

.matrix-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px; }
  .matrix-container .matrix-block {
    position: relative;
    box-sizing: border-box;
    flex-basis: 50%;
    height: auto;
    min-height: 150px;
    padding: 20px; }
  .matrix-container .title {
    display: none;
    text-align: center;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 500; }
    @media (max-width: 414px) {
      .matrix-container .title {
        display: block; } }
  .matrix-container .label-container {
    position: absolute;
    margin-left: -20px; }
    .matrix-container .label-container .label {
      padding: 5px 10px;
      background-color: white;
      color: gray;
      text-transform: uppercase; }
    @media (max-width: 414px) {
      .matrix-container .label-container {
        display: none !important; } }
  .matrix-container .label-urgently {
    display: flex;
    justify-content: center;
    bottom: -17px;
    width: 100%; }
    .matrix-container .label-urgently .label {
      display: inline-block; }
  .matrix-container .label-not-urgently {
    display: flex;
    justify-content: center;
    top: -17px;
    width: 100%; }
    .matrix-container .label-not-urgently .label {
      display: inline-block; }
  .matrix-container .label-important {
    display: inline-flex;
    align-items: center;
    top: 0;
    left: -20px;
    height: 100%; }
    .matrix-container .label-important .label {
      display: inline-table;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg); }
  .matrix-container .label-not-important {
    display: inline-flex;
    align-items: center;
    top: 0;
    right: -52px;
    height: 100%; }
    .matrix-container .label-not-important .label {
      display: inline-table;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg); }
  @media (max-width: 414px) {
    .matrix-container .matrix-block {
      flex-basis: 100%; }
    .matrix-container div {
      border: none !important; } }

.matrix-container > div:first-child {
  border-bottom: 1px solid lightgray;
  border-right: 1px solid lightgray; }

.matrix-container > div:nth-child(2) {
  border-bottom: 1px solid lightgray; }

.matrix-container > div:nth-child(3) {
  border-right: 1px solid lightgray; }

